.header {
  padding: 0 calc(50% - 1440px / 2);
  display: flex;

  &__nav {
    border: 1px solid #d3d3d3;
    box-shadow: -1px 0 0 #d3d3d3;
    width: 100%;
  }

  &__icon-container {
    width: 64px;
    height: 64px;
  }
}

@media screen and(max-width: 767px) {
  .header {
    &__nav {
      display: none;
    }
  }
}
